class Overlay {
  constructor() {
    this.overlay = document.createElement('article');
    this.overlay.classList.add('loading');

    return this.overlay;
  }
}

class Svg {
  constructor() {
    this.svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
    this.svg.classList.add('loading__svg');

    this.rect = document.createElementNS('http://www.w3.org/2000/svg', 'rect');

    this.svg.append(this.rect);

    return this.svg;
  }
}

const timeSet = (s) => {
  return new Promise((resolve) => {
    setTimeout(resolve, Number(s) * 1000);
  });
};

const loading = {
  minWaitTime: 0.3,
  maxWaitTime: 3,
  isExcuting: false,

  on: async function (waitTime = null) {
    // ÏûÖÎ†•Ìïú Ï¥àÎßåÌÅº Í∏∞Îã§Î¶∞ ÌõÑ Î°úÎî© Ïã§Ìñâ
    if (!!waitTime) {
      if (waitTime > this.maxWaitTime || typeof waitTime !== 'number') {
        throw new Error('loading.on Ìï®ÏàòÏùò Ïù∏ÏûêÍ∞Ä Ïà´ÏûêÍ∞Ä ÏïÑÎãàÍ±∞ÎÇò ÏµúÎåÄÍ∞íÏùÑ Ï¥àÍ≥ºÌñàÏäµÎãàÎã§.');
      }

      await timeSet(waitTime);
    }

    this.isExcuting = true;

    const DOMfragment = document.createDocumentFragment();

    const overlay = new Overlay();
    const svg = new Svg();

    overlay.append(svg);
    DOMfragment.append(overlay);

    document.body.append(DOMfragment);

    // ÏµúÎåÄ ÎåÄÍ∏∞ÏãúÍ∞Ñ ÌõÑÏóêÎèÑ Î°úÎî©Ïù¥ Í∫ºÏßÄÏßÄ ÏïäÏïòÎã§Î©¥ => Ï¶â, off() Ïã§ÌñâÏúºÎ°ú isExcutingÏù¥ falseÍ∞Ä ÎêòÏßÄ ÏïäÏïòÎã§Î©¥ Î°úÎî© ÏÇ≠Ï†ú ÏûêÎèô Ïã§Ìñâ
    await timeSet(this.maxWaitTime);

    if (this.isExcuting === true) {
      console.warn('loading.off Í∞Ä Ìò∏Ï∂úÎêòÏßÄ ÏïäÏïÑ Î°úÎî© Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä ÏûêÎèô ÏÇ≠Ï†úÏ≤òÎ¶¨ ÎêòÏóàÏäµÎãàÎã§.');
      this.deleteLoading();
    }
  },

  off() {
    const recursiveTimer = (s = this.minWaitTime) => {
      setTimeout(() => {
        const deleteDone = this.deleteLoading();
        if (deleteDone) {
          this.waitTimeCnt = 0;
          return;
        } //
        else {
          return this.off();
        }
      }, s * 1000);
    };

    recursiveTimer(0.25); // ÏûÖÎ†•Ìïú Ï¥à Í∞ÑÍ≤©ÏúºÎ°ú Î°úÎî© ÎÅÑÎäî Ìï®Ïàò Ìò∏Ï∂ú
  },

  deleteLoading() {
    const loadingElem = document.querySelector('.loading');

    if (!!loadingElem) {
      loadingElem.remove();
      this.isExcuting = false;

      return true;
    } //
    else {
      return false;
    }
  },
};

export default loading;
