const requestURL = {
  host: 'https://www.ddccomputer.com',
  localhost: 'http://localhost:8001',

  get url() {
    if (!this._path) {
      console.error('requestURL(): pathÎ•º ÏÑ§Ï†ïÌï¥Ï£ºÏÑ∏Ïöî');
      return;
    }
    return process.env.NODE_ENV === 'development'
      ? this.localhost + this._path
      : this.host + this._path;
  },

  set path(path) {
    this._path = path;
  },
};

export default requestURL;
